const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {
	
	// For testing
	@property(cc.Label)
	private audioTestDisplay: cc.Label = null;

	// List of seamless loop audios to play
	private seamlessAudioList: Array<cc.Class> = [];
	
	// List of fading audios to play
	private fadeAudioList: Array<cc.Class> = [];
	
	// Starting time of the lib
	private startTime : number;
	
	// FPS
	private fps : number = 0;
	
	// Frame count
	private frameCount : number = 0;
	
	// Total limiter
	private totalVolume : number = 0;
		
	// Playing list for limiter
	private audioList: Array<Integer> = [];

    // LIFE-CYCLE CALLBACKS:

    protected onLoad() {
		
		// Customized class for storing info of seamless audio
		this.SeamlessAudio = cc.Class({
			
			properties: {
				clip : cc.AudioClip,
				id: 0,
				startBuffer: 0,
				endBuffer : 0,
			},
			
			getId: function () {
				return this.id;
			},
			
			setId: function (value) {
				this.id = value;
			},
			
			getStartBuffer: function () {
				return this.startBuffer;
			},
			
			setStartBuffer: function (value) {
				this.startBuffer = value;
			},
			
			getEndBuffer: function () {
				return this.endBuffer;
			},
			
			setEndBuffer: function (value) {
				this.endBuffer = value;
			}
		});
		
		// Customized class for storing info of fading audio
		this.FadeAudio = cc.Class({
			
			properties: {
				id: 0,
				fadeRate: 0,
				endVolume: 0,
				fadeCheckTime: 0,
				endTime: 0,
			},
			
			getId: function () {
				return this.id;
			},
			
			setId: function (value) {
				this.id = value;
			},
			
			getFadeRate: function () {
				return this.fadeRate;
			},
			
			setFadeRate: function (value) {
				this.fadeRate = value;
			},
			
			getEndVolume: function () {
				return this.endVolume;
			},
			
			setEndVolume: function (value) {
				this.endVolume = value;
			},
			
			getFadeCheckTime: function () {
				return this.fadeCheckTime;
			},
			
			setFadeCheckTime: function (value) {
				this.fadeCheckTime = value;
			},
			
			getEndTime: function () {
				return this.endTime;
			},
			
			setEndTime: function (value) {
				this.endTime = value;
			}
		});
		
		// Starting time of the lib
		this.startTime = (new Date()).getTime();
		
		// For testing
		// this.load();
		cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
		cc.resources.load("Sounds/spark_man", function (err, clip) {
			self.clip_1 = clip;
		});
		cc.resources.load("Sounds/Loop", function (err, clip) {
			self.clip_2 = clip;
		});
		this.markerTest = false;
		this.totalLimiter(-2);
	}
	
	// For testing
	// protected load() 
	// {
		// let url = "Sounds/spark_man";
		// let loop = true;
	    // let volume = 1;
		// var self = this;
		// cc.resources.load(url, function (err, clip) {
			// let path = clip.nativeUrl;
			// let id = self.play(clip, true, 1);
			// self.playSeamlessAudio(id, 150, 440);
			// self.playFadeAudio(id, 0, 0.9, 10000);
			// self.playAtMarker(id, "0:1:0:0");
		// });
	// }
	
	// Limiter to avoid clipping and distortion when many audios playing at once
	// Param:
	//		clip: audio file (cc.AudioClip)
	//		volume: audio volume to play (0.0 ~ 1.0)
	//		threshold: limited db (float)
	protected limiter(clip, volume, threshold)
	{
		var audio = document.createElement('audio');
		audio.src = clip.nativeUrl;
		var context = new AudioContext();
		var source = context.createMediaElementSource(audio);
		var preGain = context.createGain();
		var limiter = context.createDynamicsCompressor();
		limiter.threshold.value = threshold; // this is the pitfall, leave some headroom
		limiter.knee.value = 0.0; // brute force
		limiter.ratio.value = 20.0; // max compression
		limiter.attack.value = 0.005; // 5ms attack
		limiter.release.value = 0.050; // 50ms release
		source.connect(preGain);
		preGain.connect(limiter);
		limiter.connect(context.destination);
		threshold /= 20;
		let newVolume = Math.pow(10, threshold);
		console.log("New volume: " + newVolume);
		if (newVolume < volume)
			return newVolume;
		if (volume > 1)
			return 1;
		else
			return volume;
	}
	
	// Total limiter for all playing audios
	// Param:
	//		threshold: limited db (float)
	protected totalLimiter(threshold)
	{
		threshold /= 20;
		this.totalVolume = Math.pow(10, threshold);
		console.log("Limiter: " + this.totalVolume);
	}
	
	// Automatically set limiter for all audios which are playing at the moment
	protected setLimiterAuto()
	{
		if (this.totalVolume > 0 && this.audioList.length > 0)
		{
			this.totalVolume /= this.audioList.length;
			let orgTotalVolume = 0;
			for (let i = 0; i < this.audioList.length; i++)
			{
				if (cc.audioEngine.getState(i) == 1)
				{
					orgTotalVolume += cc.audioEngine.getVolume(i);
				}
			}
			console.log("Original total volume: " + orgTotalVolume);
			let rate = this.totalVolume / orgTotalVolume;
			console.log("Rate: " + rate);
			for (let i = 0; i < this.audioList.length; i++)
			{
				console.log("Old volume " + i + " : " + cc.audioEngine.getVolume(i));
				let newVolume = cc.audioEngine.getVolume(i) * rate;
				if (newVolume < cc.audioEngine.getVolume(i))
					cc.audioEngine.setVolume(this.audioList[i], newVolume);
				console.log("New volume " + i + " : " + cc.audioEngine.getVolume(i));
			}
			this.totalVolume *= this.audioList.length;
		}
	}
	
	// Customized audio playing function from original cocos engine to add limiter
	// Param:
	//		clip: audio file (cc.AudioClip)
	//		loop: looping (boolean)
	//		volume: audio volume to play (0.0 ~ 1.0)
	//		volume: audio volume to play (0.0 ~ 1.0)
	//		threshold: limited db (float)
	protected play(clip, loop, volume, threshold)
	{
		if (this.totalVolume == 0)
		{
			return cc.audioEngine.play(clip, loop, this.limiter(clip, volume, threshold));
		}
		else
		{
			let id = cc.audioEngine.play(clip, loop, this.limiter(clip, volume, threshold));
			this.audioList.push(id);
			return id;
		}
	}
	
	// Get format of audio file (mp3, ogg, wav)
	// Param:
	//		clip: audio file (cc.AudioClip)
	protected getFormat(clip)
	{
		return clip.nativeUrl.substr(clip.nativeUrl.length - 3, 3).toLowerCase();
	}
	
	// Init and add new instance of Seamless audio class to seamless loop list
	// Param:
	//		id: id of the playing audio (integer)
	//		startBuffer: time to skip from the begining (milisecond)
	//		endBuffer: time to trim from the end (milisecond)
	protected playSeamlessAudio(id, startBuffer, endBuffer)
	{
		startBuffer /= 1000;
		endBuffer /= 1000;
		let audio = new this.SeamlessAudio();
		audio.setId(id);
		audio.setStartBuffer(startBuffer);
		audio.setEndBuffer(endBuffer);
		this.seamlessAudioList.push(audio);
	}
	
	// Control the timing of all the seamless loop audios
	protected seamlessProcess()
	{
		for (let i = 0; i < this.seamlessAudioList.length; i++)
		{
			if (cc.audioEngine.isLoop(this.seamlessAudioList[i].getId())
				&& cc.audioEngine.getState(this.seamlessAudioList[i].getId()) == 1
				&& cc.audioEngine.getCurrentTime(this.seamlessAudioList[i].getId()) > cc.audioEngine.getDuration(this.seamlessAudioList[i].getId()) - this.seamlessAudioList[i].getEndBuffer()
				)
			{
				cc.audioEngine.setCurrentTime(this.seamlessAudioList[i].getId(), this.seamlessAudioList[i].getStartBuffer());
			}
		}
	}
	
	// Init and add new instance of FadeAudio class to fading audio list
	// Param:
	//		id: id of the playing audio (integer)
	//		startVolume: starting volume of the audio from the begining of fading (0.0 ~ 1.0)
	//		endVolume: final volume of the audio at the end of fading (0.0 ~ 1.0)
	// 		requiredTime: desired time to reach the final volume (milisecond)
	protected playFadeAudio(id, startVolume, endVolume, requiredTime)
	{
		if (this.fps > 0)
		{
			let remainingTime = (cc.audioEngine.getDuration(id) - cc.audioEngine.getCurrentTime(id)) * 1000;
			if (requiredTime == 0 || remainingTime < requiredTime)
			{
				requiredTime = remainingTime;
			}
			cc.audioEngine.setVolume(id, startVolume);
			let audio = new this.FadeAudio();
			audio.setId(id);
			audio.setEndVolume(endVolume);
			audio.setFadeCheckTime(new Date().getTime());
			audio.setEndTime(audio.getFadeCheckTime() + requiredTime);
			this.updateFadeRate(audio);
			this.fadeAudioList.push(audio);
		}
	}
	
	// Calculate the volume changing rate of each frame based on fps and remaining required time
	// Param:
	//		audio: audio file (cc.AudioClip)
	protected updateFadeRate(audio)
	{
		let remainTime = audio.getEndTime() - (new Date()).getTime();
		let totalFrame = (remainTime/ 1000) * this.fps;
		let volumeGap = audio.getEndVolume() - cc.audioEngine.getVolume(audio.getId());
		audio.setFadeRate(volumeGap / totalFrame);
	}
	
	// Control the volume of all audios in fading process
	protected fadeProcess()
	{
		for (let i = 0; i < this.fadeAudioList.length; i++)
		{
			if (cc.audioEngine.getVolume(this.fadeAudioList[i].getId()) != this.fadeAudioList[i].getEndVolume())
			{
				if (this.fadeAudioList[i].getFadeRate() > 0)
				{
					if (cc.audioEngine.getVolume(this.fadeAudioList[i].getId()) > this.fadeAudioList[i].getEndVolume())
					{
						cc.audioEngine.setVolume(this.fadeAudioList[i].getId(), this.fadeAudioList[i].getEndVolume());
						continue;
					}
				}
				else if (this.fadeAudioList[i].getFadeRate() < 0)
				{
					if (cc.audioEngine.getVolume(this.fadeAudioList[i].getId()) < this.fadeAudioList[i].getEndVolume())
					{
						cc.audioEngine.setVolume(this.fadeAudioList[i].getId(), this.fadeAudioList[i].getEndVolume());
						continue;
					}
				}
				this.updateFadeRate(this.fadeAudioList[i]);
				cc.audioEngine.setVolume(this.fadeAudioList[i].getId(), cc.audioEngine.getVolume(this.fadeAudioList[i].getId()) + this.fadeAudioList[i].getFadeRate());
			}
			// console.log("Volume at " + ((new Date()).getTime() - this.fadeAudioList[i].getFadeCheckTime()) + " " + cc.audioEngine.getVolume(this.fadeAudioList[i].getId()));
		}
	}
	
	// Get fps
	protected getFPS()
	{
		if ((new Date()).getTime() - this.startTime >= 1000)
		{
			this.fps = this.frameCount;
			this.frameCount = 0;
			this.startTime = (new Date()).getTime();
			// console.log("FPS: " + this.fps);
		}
		this.frameCount += 1;
	}
	
	// Convert marker to specific time within audio duration
	// Param:
	//		marker: desired time to play the audio with format "hh:mm:ss:SSS" 
	protected markerToTimeInSec(marker)
	{
		let timeArray = marker.split(":");
		let hour = 0;
		let min = 0;
		let sec = 0;
		let milSec = 0;
		if (timeArray.length == 4)
		{
			hour = parseInt(timeArray[0]);
			min = parseInt(timeArray[1]);
			sec = parseInt(timeArray[2]);
			milSec = parseInt(timeArray[3]);
		}
		else if (timeArray.length == 3)
		{
			min = parseInt(timeArray[0]);
			sec = parseInt(timeArray[1]);
			milSec = parseInt(timeArray[2]);
		}
		else if (timeArray.length == 2)
		{
			sec = parseInt(timeArray[0]);
			milSec = parseInt(timeArray[1]);
		}
		else if (timeArray.length == 1)
		{
			milSec = parseInt(timeArray[0]);
		}
		let totalSec = hour * 3600 + min * 60 + sec + (milSec / 1000);
		return totalSec;
	}
	
	// Play the audio at desired time with marker
	// Param:
	//		marker: desired time to play the audio with format "hh:mm:ss:SSS"
	protected playAtMarker(id, marker)
	{
		cc.audioEngine.setCurrentTime(id, this.markerToTimeInSec(marker));
	}
	
	protected getCurrentTimeMarker(id)
	{
		let currentTime = cc.audioEngine.getCurrentTime(id);
		let hour = Math.floor(currentTime / 3600);
		// console.log("hour " + hour);
		let min = Math.floor(currentTime / 60);
		if (min > 59)
			min = 59;
		// console.log("min " + min);
		let sec = Math.floor(currentTime - hour * 3600 - min * 60);
		//console.log("sec " + sec);
		let milSec = currentTime - Math.floor(currentTime);
		// console.log("milsec " + milSec);
		milSec *= 1000;
		if (milSec > 999)
			milSec = 999;
		let marker = "" + hour + ":" + min + ":" + sec + ":" + Math.round(milSec);
		return marker;
	}
	
	onKeyDown (event) {
        // set a flag when key pressed
        switch(event.keyCode) {
			case cc.macro.KEY.q:
				this.markerTest = false;
				cc.audioEngine.stopAll();
				this.id = this.play(self.clip_2, true, 1, 0);
				this.playSeamlessAudio(this.id, 0, 0);
				console.log("Normal loop");
				this.audioTestDisplay.string = "Normal loop";
                break;
			case cc.macro.KEY.w:
				this.markerTest = false;
				cc.audioEngine.stopAll();
				this.id = this.play(self.clip_2, true, 1, 0);
				this.playSeamlessAudio(this.id, 0, 0);
				console.log("Seamless loop");
				this.audioTestDisplay.string = "Seamless loop";
                break;
			case cc.macro.KEY.e:
				this.markerTest = false;
				cc.audioEngine.stopAll();
				this.id = this.play(self.clip_1, true, 1, 0);
				this.playFadeAudio(this.id, 0, 1, 20000);
				console.log("Fade in");
				this.audioTestDisplay.string = "Fade in in 20 secs";
                break;
			case cc.macro.KEY.r:
				this.markerTest = false;
				cc.audioEngine.stopAll();
				this.id = this.play(self.clip_1, true, 1, 0);
				this.playFadeAudio(this.id, 1, 0, 15000);
				console.log("Fade out");
				this.audioTestDisplay.string = "Fade out in 15 secs";
                break;
			case cc.macro.KEY.t:
				this.markerTest = false;
				cc.audioEngine.stopAll();
				this.id = this.play(self.clip_1, true, 1, 0);
				this.playAtMarker(this.id, "0:1:2:0");
				console.log("Play at 0:1:2:0");
				this.audioTestDisplay.string = "Play audio at 0:1:2:0";
                break;
			case cc.macro.KEY.y:
				this.markerTest = false;
				cc.audioEngine.stopAll();
				this.id = this.play(self.clip_1, true, 1, 0);
				this.playAtMarker(this.id, "0:2:5:50");
				console.log("Play at 0:2:5:50");
				this.audioTestDisplay.string = "Play audio at 0:2:5:50";
                break;
			case cc.macro.KEY.u:
				this.markerTest = false;
				cc.audioEngine.stopAll();
				this.play(self.clip_1, true, 1, -2);
				this.playAtMarker(this.play(self.clip_1, true, 0.5, -2), "0:2:5:50");
				this.playAtMarker(this.play(self.clip_1, true, 0.5, -2), "0:0:55:50");
				console.log("Limiter");
				this.audioTestDisplay.string = "Limiter";
                break;
			case cc.macro.KEY.i:
				if (!this.markerTest)
				{
					cc.audioEngine.stopAll();
					this.id = this.play(self.clip_1, true, 1, -2);
					let currentTime = this.getCurrentTimeMarker(this.id);
					console.log("Playing at " + currentTime);
					this.audioTestDisplay.string = "Playing at " + currentTime;
					this.markerTest = true;
				}
				else
				{
					let currentTime = this.getCurrentTimeMarker(this.id);
					console.log("Playing at " + currentTime);
					this.audioTestDisplay.string = "Playing at " + currentTime;
				}
				break;
        }
    }

    protected update(dt: number) {
		this.getFPS();
		this.seamlessProcess();
		this.fadeProcess();
		this.setLimiterAuto();
	}
}
